import React, { useEffect, useRef } from "react";
import { refreshWidgetData } from "./services/api/refreshService";
import useToast from "./hooks/useToast";
import useDroppableArea from "./hooks/useDroppableArea";
import { MSG_REFRESH_ERROR, MSG_REFRESH_SUCCESS } from "./utils/toastMessages";
import store from "./store"; // âœ… Import Redux store directly

const WidgetLifecycle = () => {
  const { handleDrop } = useDroppableArea();
  const { showSuccessToast, showErrorToast } = useToast();
  const hasOnRefreshRun = useRef(false); // âœ… Prevent duplicate onRefresh execution

  console.log("---[WidgetLifecycle]--- starts");

  const isAutoTriggeredRefresh = (trace) => {
    return trace.some((line) =>
      line.includes("UWA_Frame_Alone.js") || line.includes("bundle-min.js")
    );
  };

  useEffect(() => {
    if (!window.widget) return;

    console.log("[WidgetLifecycle] ðŸ“Œ Registering `onRefresh` event...");
    
    const onRefresh = async () => {
      const trace = new Error().stack.split("\n");
      
      if (isAutoTriggeredRefresh(trace)) {
        console.warn("[WidgetLifecycle] â›” Auto-refresh detected. Ignoring unwanted `onRefresh`.");
        return; // âœ… Block auto-triggered refresh
      }

      if (hasOnRefreshRun.current) {
        console.warn("[WidgetLifecycle] â³ `onRefresh` was already executed. Ignoring duplicate trigger.");
        return;
      }
      hasOnRefreshRun.current = true; // âœ… Mark as executed

      console.log("[WidgetLifecycle] ðŸ”„ `onRefresh` triggered!");
      const latestState = store.getState();
      const latestDraggedData = latestState.droppedObject.droppedObjectData.initialDraggedData;

      if (!latestDraggedData?.data?.items?.length) {
        console.error("[WidgetLifecycle] âš ï¸ `initialDraggedData` is missing or invalid:", latestDraggedData);
        return;
      }

      try {
        console.log("[WidgetLifecycle] ðŸš€ Refreshing widget with latest data...");
        await refreshWidgetData(latestDraggedData.data.items, handleDrop);
        showSuccessToast(MSG_REFRESH_SUCCESS);
      } catch (error) {
        console.error("[WidgetLifecycle] âŒ Error during refresh:", error);
        showErrorToast(MSG_REFRESH_ERROR);
      }
    };

    window.widget.addEvent("onRefresh", onRefresh);
    console.log("[WidgetLifecycle] âœ… `onRefresh` event registered successfully");
  }, []); // âœ… Runs only once

  return null;
};

export default WidgetLifecycle;
